.period-description {
	margin-bottom: 40px;

	@media (min-width: $desktop-width) {
    margin-bottom: 80px;
  }
}

.period-description__title {
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.7px;
  margin-bottom: 16px;

  @media (min-width: $desktop-width) {
    font-size: 48px;
    line-height: 56px;
    margin-bottom: 20px;
  }
}

.period-description__text {
	font-family: "Manrope", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 26px;

	@media (min-width: $desktop-width) {
    width: 872px;
  }
}
